import React, { Component } from 'react';
import { Badge, Card, CardBody, CardHeader, Col, Row, Table, Button, Form,
    FormGroup,
    Input,
    InputGroup,
    InputGroupAddon,
    Label,
    UncontrolledPopover,
    PopoverBody,
    PopoverHeader,
    Spinner
} from 'reactstrap';
import 'react-dates/initialize';
import { DateRangePicker, SingleDatePicker, DayPickerRangeController } from 'react-dates';
import 'react-dates/lib/css/_datepicker.css';

import {connect} from 'react-redux'
import {getPromoCodes, createPromoCode, updatePromoCode, deletePromoCode, setError} from '@redux/actions'
import Modal from '@utils/Modal'
import _ from 'lodash'
import Paginationbs4 from "react-js-pagination";
//https://www.npmjs.com/package/react-js-pagination-bs4
import Errors from '@utils/Errors'
import ReactSelect from 'react-select'
//https://github.com/JedWatson/react-select
import {
    FETCH_PROMO_CODES,
    FETCH_PROMO_CODES_SUCCESS,
    FETCH_PROMO_CODES_ERROR,
    UPDATE_PROMO_CODES,
    UPDATE_PROMO_CODES_SUCCESS,
    UPDATE_PROMO_CODES_ERROR,
    ADD_PROMO_CODES,
    ADD_PROMO_CODES_SUCCESS,
    ADD_PROMO_CODES_ERROR,
    DELETE_PROMO_CODES,
    DELETE_PROMO_CODES_SUCCESS,
    DELETE_PROMO_CODES_ERROR,
    PROMO_CODES_ERROR_REMOVE
} from '@redux/types'

import moment from 'moment'


class PromoCodes extends Component {
  state = {
      promo_code: null,
      currentFilter: null,
      isFilterFormVisible: false,
      created_at: [],
      validity: []
  }

    componentDidMount(){
      this.getPromoCodes()
    }

    modalClose(){
        this.setState({promo_code: null})
    }

    modalSubmit(){
      //console.log('User: '  , this.state.promo_code)
        if(this.state.promo_code._id)
        {
            this.props.updatePromoCode(this.state.promo_code, () => {

                this.modalClose()
            })
        }else{
            this.props.createPromoCode(this.state.promo_code, () => {

                this.modalClose()
            })
        }

    }

    getPromoCodes(sortBy, filter, add = 1){
        let filters = {...this.props.promo_codes.filters};
        if(add === 1){
            if(filter === 'created_at' || filter === 'validity'){
                filters[filter] = [this.state[filter][0].format('YYYY-MM-DD'), this.state[filter][1].format('YYYY-MM-DD')];
            }else{
                filters[filter] = this.state[filter];
            }
        }else if(add === 0){
            delete filters[filter];
        }


        const sortDir = sortBy === this.props.promo_codes.sortBy?
            (this.props.promo_codes.sortDir === 1? -1: 1):this.props.promo_codes.sortDir;
        this.props.getPromoCodes({
            page: this.props.promo_codes.list.current_page,
            sortBy: sortBy? sortBy: this.props.promo_codes.sortBy,
            sortDir: sortDir,
            filters: filters
        })
    }

    getDirectionIcon(sortBy){
        if(sortBy !== this.props.promo_codes.sortBy){return ;}
        return <i className={this.props.promo_codes.sortDir === 1?'fa fa-level-up ml-2':'fa fa-level-down ml-2'}></i>
    }

    //<Icon type={this.props.promo_codes.sortDir === 1?'arrow-top': 'arrow-bottom'} />


    onModalChange(value, key){
        this.state.promo_code[key] = value
        this.setState({promo_code: {...this.state.promo_code}})
    }

    setSelected(index){
        this.props.setError()
        this.setState({promo_code: {...this.props.promo_codes.list.data[index]}})
    }


    renderFilterForm(){
      if(this.state.isFilterFormVisible){
          return (
              <div className="min-width23em p-3 rounded shadow border bg-white">
                  <p className='lead text-center'>Select the column</p>
                  <form>
                      <div className='form-group mb-0'>
                          <select className='form-control filter_option' onChange={(e) => this.setState({currentFilter: e.target.value})}>
                              <option>Choose</option>
                              <option value={'created_at'}>Created At</option>
                              <option value={'validity'}>Validity</option>
                              <option value={'discount'}>Discount</option>
                              <option value={'code'}>Code</option>
                          </select>
                      </div>
                  </form>
                  {this.state.currentFilter === 'created_at' &&
                  <div className='daterangepicker drp_dropdown mt-3'>
                      <DateRangePicker
                          startDate={this.state.created_at[0]} // momentPropTypes.momentObj or null,
                          startDateId="your_unique_start_date_id" // PropTypes.string.isRequired,
                          endDate={this.state.created_at[1]} // momentPropTypes.momentObj or null,
                          endDateId="your_unique_end_date_id" // PropTypes.string.isRequired,
                          onDatesChange={({ startDate, endDate }) => this.setState({ created_at: [startDate, endDate] })} // PropTypes.func.isRequired,
                          focusedInput={this.state.focusedInput} // PropTypes.oneOf([START_DATE, END_DATE]) or null,
                          onFocusChange={focusedInput => this.setState({ focusedInput })} // PropTypes.func.isRequired,
                          enableOutsideDays={true}
                          isOutsideRange={() => false}
                      />


                  </div>
                  }
                  {this.state.currentFilter === 'validity' &&
                  <div className='daterangepicker drp_dropdown mt-3'>
                      <DateRangePicker
                          startDate={this.state.validity[0]} // momentPropTypes.momentObj or null,
                          startDateId="your_unique_start_date_id" // PropTypes.string.isRequired,
                          endDate={this.state.validity[1]} // momentPropTypes.momentObj or null,
                          endDateId="your_unique_end_date_id" // PropTypes.string.isRequired,
                          onDatesChange={({ startDate, endDate }) => this.setState({ validity: [startDate, endDate] })} // PropTypes.func.isRequired,
                          focusedInput={this.state.focusedInput} // PropTypes.oneOf([START_DATE, END_DATE]) or null,
                          onFocusChange={focusedInput => this.setState({ focusedInput })} // PropTypes.func.isRequired,
                          enableOutsideDays={true}
                          isOutsideRange={() => false}
                      />

                  </div>
                    }
                  {this.state.currentFilter === 'discount' &&
                  <div>
                      <Input type="number" className='mt-3 discount_field' onChange={(e) => this.setState({discount: e.target.value})} value={this.state.discount} placeholder="Enter Discount"  />

                  </div>
                  }
                  {this.state.currentFilter === 'code' &&
                  <div>
                      <Input type='text' placeholder="Enter Code" className='mt-3' value={this.state.code} onChange={(e) => this.setState({code: e.target.value})}/>

                  </div>
                  }

                  <div className='mt-3 d-flex justify-content-between'>

                      <Button color="secondary" onClick={() => {this.setState({isFilterFormVisible: false});}}>Cancel</Button>
                      {this.state.currentFilter &&
                      <Button color="primary" onClick={() => {
                          this.setState({isFilterFormVisible: false});
                          this.getPromoCodes(null, this.state.currentFilter)
                      }}>
                          Add Filter
                      </Button>
                      }
                  </div>


              </div>
          )
      }

    }


  render() {
    //console.log(this.props)


      const { promo_codes } = this.props
    return (
      <div className="animated fadeIn">
        <Row>
          <Col xs="12" lg="12">
            <Card>
              <CardHeader>
                <i className="fa fa-cc"></i> Promo Codes List
              </CardHeader>

                <CardBody className='custom_cardbody'>
                    <div className='d-flex align-items-center justify-content-between'>
                        <div>
                            <Button onClick={(e) => {
                                this.props.setError()
                                this.setState({promo_code: {}})}}
                                    className="btn btn-info mb-1 text-white"><i className='fa fa-plus mr-1'></i> Add New Code</Button>
                        </div>

                        <div>
                            {(promo_codes.type == FETCH_PROMO_CODES) &&
                            <Spinner type="grow" color="primary" style={{ width: '1.5rem', height: '1.5rem' }} />
                            }
                        </div>
                        <div className='mr-5'></div>
                    </div>
                    <div className='mb-1'>
                        <div className='d-flex justify-content-between align-items-center position-relative'>
                            <div className='d-flex align-items-center'>
                                {Object.keys(this.props.promo_codes.filters).map((filter) => {
                                    if(filter === 'undefined'){return;}
                                    return <div className='mr-2'>
                                        <Button className="btn btn-secondary" onClick={() => this.getPromoCodes(null, filter, 0)}>
                                            {filter.toUpperCase()}: ${this.props.promo_codes.filters[filter]} <i className='fa fa-times ml-1'></i>
                                        </Button>
                                    </div>
                                })}


                            </div>
                            <Button
                                onClick={(e) => {this.setState({isFilterFormVisible: !this.state.isFilterFormVisible})}}
                                className="btn btn-info mb-1 text-white">
                                <i className='fa fa-filter mr-1'></i>Filter
                            </Button>
                        </div>
                        <div className="position-absolute custom_filter">{this.renderFilterForm()}</div>
                    </div>


                  <Table responsive striped>
                  <thead>
                  <tr>
                    <th onClick={() => this.getPromoCodes('created_at')}>Created On{this.getDirectionIcon('created_at')}</th>
                    <th onClick={() => this.getPromoCodes('valid_from')}>Validity{this.getDirectionIcon('valid_from')}</th>
                    <th onClick={() => this.getPromoCodes('discount')}>Discount{this.getDirectionIcon('discount')}</th>
                    <th onClick={() => this.getPromoCodes('code')}>Code{this.getDirectionIcon('code')}</th>
                    <th>Actions</th>
                  </tr>
                  </thead>
                  <tbody>
                  {promo_codes.list.data && promo_codes.list.data.map((data, index) => {
                      return (
                          <tr key={index}>
                            <td>{moment(data.created_at).format('DD-MMM-YYYY')}</td>
                              {/*to show time add hh:mm a at the end of DD*/}
                            <td>{moment(data.valid_from).format('DD-MMM-YYYY')} to {moment(data.valid_to).format('DD-MMM-YYYY')}</td>
                            <td>{data.discount}</td>
                            <td>{data.code}</td>

                            <td>
                              <Button className='mr-2' onClick={(e) => this.setSelected(index)} color="secondary"><i className='fa fa-pencil'></i></Button>
                              <Button onClick={(e) => this.props.deletePromoCode(data._id)} color="danger"><i className='fa fa-remove'></i></Button>
                            </td>
                        </tr>
                      )
                  })}
                  </tbody>
                </Table>
                   <Paginationbs4
                      activePage={promo_codes.list.current_page}
                      totalItemsCount={promo_codes.list.total}
                      onChange={(page) => this.props.getPromoCodes({page})}
                  />
              </CardBody>

            </Card>
          </Col>
        </Row>
          {this.state.promo_code &&
          <Modal
              className='custom_modal'
              title={this.state.promo_code._id?'Edit Code'+this.state.promo_code.name: 'Add New Code'}
              open={true}
              footer={
              <div>
                  <Button className="mr-1" color="success" onClick={(e) => this.modalSubmit()}>
                      Submit
                      {(promo_codes.type == ADD_PROMO_CODES || promo_codes.type == UPDATE_PROMO_CODES) &&
                      <Spinner size="sm" color="secondary"  />
                      }
                  </Button>
                  <Button color="secondary" onClick={() => this.modalClose()}>Cancel</Button>
              </div>
          }>
              {promo_codes.error && <Errors errors={promo_codes.error[0]} />}
              <Form className="form-horizontal">
                  <FormGroup row>
                      <Col md="3">
                          <Label htmlFor="text-input">Code</Label>
                      </Col>
                      <Col xs="12" md="9">
                          <Input type="text" onChange={(e) => this.setState({promo_code: {...this.state.promo_code, code: e.target.value}})} value={this.state.promo_code.code} placeholder="Enter Promo Code" />
                      </Col>
                  </FormGroup>
                  <FormGroup row>
                      <Col md="3">
                          <Label htmlFor="text-input">Discount</Label>
                      </Col>
                      <Col xs="12" md="9">
                          <InputGroup>
                              <InputGroupAddon addonType="prepend">$</InputGroupAddon>
                              <Input type="number" className='discount_field' onChange={(e) => this.setState({promo_code: {...this.state.promo_code, discount: e.target.value}})} value={this.state.promo_code.discount} placeholder="Amount"  />
                          </InputGroup>
                      </Col>
                  </FormGroup>
                  <FormGroup row className='daterangepicker'>
                      <Col md="3">
                          <Label htmlFor="text-input">Validity</Label>
                      </Col>
                      <Col xs="12" md="9">
                          <DateRangePicker
                              startDate={moment(this.state.promo_code.valid_from)}
                              startDateId="your_unique_start_date_id"
                              endDate={moment(this.state.promo_code.valid_to)}
                              endDateId="your_unique_end_date_id"
                              onDatesChange={({ startDate, endDate }) => this.setState({ promo_code: {...this.state.promo_code, valid_from: startDate, valid_to: endDate }})}
                              focusedInput={this.state.focusedInput}
                              onFocusChange={focusedInput => this.setState({ focusedInput })}
                          />
                      </Col>
                  </FormGroup>
              </Form>
        </Modal>}
      </div>

    );
  }
}

const mapStateToProps = ({promo_codes}, ownProps) => ({promo_codes})
export default connect(mapStateToProps, {getPromoCodes, createPromoCode, updatePromoCode, deletePromoCode, setError})(PromoCodes)
